import React from "react";
import { useStore } from "./App";
import ItemCounter from "./item-counter";
import Checkout from "./checkout";

const Cart = () => {
  const { store, setStoreData } = useStore();

  const total = store?.cart.reduce(
    (sum, item) => sum + item.quantity * item.price,
    0
  );

  const updateQuantity = (item, quantity) => {
    const cart = [...store.cart];
    const cartItemIndex = store.cart.findIndex((i) => i.id === item.id);

    if (cartItemIndex > -1) {
      quantity
        ? cart.splice(cartItemIndex, 1, { ...item, quantity })
        : cart.splice(cartItemIndex, 1);
    }

    setStoreData({
      ...store,
      cart,
    });
  };

  const removeItem = (item) => {
    const cart = [...store.cart];
    const cartItemIndex = store.cart.findIndex((i) => i.id === item.id);
    cart.splice(cartItemIndex, 1);
    setStoreData({
      ...store,
      cart,
    });
  };

  const checkout = async () => {
    window.$('#checkoutModal').modal('show');
  };

  return (
    <div className="cart-container">
      <div className="cart-header">
        <div className="cart-title">
          <span>Your Cart</span>
        </div>
        <div className="cart-close" data-bs-dismiss="offcanvas">
          <span>X</span>
        </div>
      </div>

      <div className="cart-body">
        {store.cart.length ? (
          store?.cart.map((item) => (
            <div key={item.id} className="cart-item">
              <div className="cart-item-thumb">
                <img src={item.imageUrl} alt={item.name} />
              </div>
              <div className="cart-item-description">
                <span className="cart-item-name">{item.name}</span>
                <span className="cart-item-price">{item.price}</span>
                <span
                  className="cart-item-remove"
                  onClick={() => removeItem(item)}
                >
                  Remove
                </span>
              </div>
              <div className="cart-item-counter">
                <ItemCounter
                  quantity={item.quantity}
                  className="cart-counter"
                  updateQuantity={(quantity) => updateQuantity(item, quantity)}
                />
              </div>
            </div>
          ))
        ) : (
          <div className="empty-cart">Your cart is empty.</div>
        )}
      </div>
      <div className="sub-total">
        <span className="sub-total-title">Subtotal :</span>
        <span className="sub-total-price">
          € {parseFloat(total).toFixed(2)}
        </span>
      </div>
      <div className="cart-footer">
        <button onClick={() => checkout()}>
          <span className="cart-button">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="#887e7e"
              className="bi bi-cart-plus"
              viewBox="0 0 16 16"
            >
              <path d="M9 5.5a.5.5 0 0 0-1 0V7H6.5a.5.5 0 0 0 0 1H8v1.5a.5.5 0 0 0 1 0V8h1.5a.5.5 0 0 0 0-1H9V5.5z" />
              <path d="M.5 1a.5.5 0 0 0 0 1h1.11l.401 1.607 1.498 7.985A.5.5 0 0 0 4 12h1a2 2 0 1 0 0 4 2 2 0 0 0 0-4h7a2 2 0 1 0 0 4 2 2 0 0 0 0-4h1a.5.5 0 0 0 .491-.408l1.5-8A.5.5 0 0 0 14.5 3H2.89l-.405-1.621A.5.5 0 0 0 2 1H.5zm3.915 10L3.102 4h10.796l-1.313 7h-8.17zM6 14a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm7 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
            </svg>
          </span>
          <span className="add-to-cart-text">Proceed to checkout</span>
        </button>
      </div>
      <Checkout />
    </div>
  );
};

export default Cart;
