const ItemCounter = ({ quantity = 1, updateQuantity, className = 'item-counter' }) => {
    return (
        <div className={className}>
            <button className={`${className}-dec`} disabled={quantity <= 1} onClick={() => updateQuantity(quantity - 1)}> -</button>
            <span className={`${className}-val`}> {quantity} </span>
            <button className={`${className}-inc`} onClick={() => updateQuantity(quantity + 1)}> + </button>
        </div>
    )
}


export default ItemCounter;