const API_URL = process.env.REACT_APP_API_URL || 'https://api.regdelivery.de';

const GET = async (endpoint) => {
    const response = await fetch(endpoint);
    return response.json();
}

const POST = async (endpoint, data) => {
    const response = await fetch(endpoint, { method: 'POST', body: JSON.stringify(data)});
    return response.json();
}

/**
 * API Endpoints
 */

export const getItems = (searchQuery = '') => {
    return GET(`${API_URL}/items?query=${searchQuery}`);
}

export const getShopDetails = () => {
    return GET(API_URL);
}

export const createCharge = (orderData, payment_method) => {
    return POST(`${API_URL}/charge/?apikey=test_api_key&payment_method=${payment_method}`, orderData);
}
