import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";
import { createCharge } from "./api";
import { useStore } from "./App";

const Checkout = () => {
  const stripe = useStripe();
  const elements = useElements();
  const { store } = useStore();

  const handleSubmit = async (event) => {
    event.preventDefault();
    
    window.$('.pay-button').prop('disabled', true);
    window.$('#pay-loading').show();
    window.$('.pay-button-text').hide();


    if (!stripe || !elements) {
      return;
    }

    // setTimeout(() => {
    //   window.$('#checkoutModal').modal('hide');
    //   document.body.click();
    //   window.$('#charge-failure-alert').show();
    //   window.$('.pay-button').prop('disabled', false);
    //   window.$('#pay-loading').hide();
    //   window.$('.pay-button-text').show();
    // }, 2000);

    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement)
    });

    if (error) {
      window.$('#charge-failure-alert').show();
      setTimeout(() => {
        window.$('#charge-failure-alert').hide();
        window.$('.pay-button').prop('disabled', false);
        window.$('#pay-loading').hide();
        window.$('.pay-button-text').show();
      }, 3000);
    } else {
      const cart = [...store.cart];
      const itemlist = {};
      cart.forEach(item => itemlist[item.id] = item.quantity);

      const charge = await createCharge({
        itemlist,
      }, paymentMethod.id);
      
      if(charge) {
        window.$('#checkoutModal').modal('hide');
        window.$('.cart-close').click()
        window.$('#charge-success-alert').show();
        setTimeout(() => {
          window.$('#charge-success-alert').hide();
          window.$('.pay-button').prop('disabled', false);
          window.$('#pay-loading').hide();
          window.$('.pay-button-text').show();
        }, 3000);
      }
    }
  };

  return (
    <div
      className="modal fade"
      id="checkoutModal"
      aria-hidden="true"
      aria-labelledby="checkoutModallabel"
      tabIndex="-1"
    >
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">Enter your Credit Card Details</h5>
          </div>
          <div className="modal-body">
            <CardElement options={{ hidePostalCode: true }} />
          </div>
          <div className="modal-footer">
            <button className="btn btn-primary pay-button" onClick={handleSubmit}>
              <span className="pay-button-text">Pay</span>
              <span className="pay-loading" id="pay-loading">
                <svg
                  version="1.1"
                  id="loader-1"
                  xmlns="http://www.w3.org/2000/svg"
                  x="0px"
                  y="0px"
                  width="40px"
                  height="40px"
                  viewBox="0 0 50 50"
                  enableBackground="new 0 0 50 50"
                >
                  <path
                    fill="#fff"
                    d="M43.935,25.145c0-10.318-8.364-18.683-18.683-18.683c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615c8.072,0,14.615,6.543,14.615,14.615H43.935z"
                  >
                    <animateTransform
                      attributeType="xml"
                      attributeName="transform"
                      type="rotate"
                      from="0 25 25"
                      to="360 25 25"
                      dur="0.6s"
                      repeatCount="indefinite"
                    />
                  </path>
                </svg>
              </span>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Checkout;
