import React, { useEffect, useState, useCallback, useContext } from "react";
import { Elements as StripeProvider } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import Layout from "./layout";
import Items from "./items";
import { getItems } from "./api";

const stripePromise = loadStripe('pk_test_Yle4AOLnKeejMpFv0WzVsRdq00CbmvTuaS');


export const StoreContext = React.createContext({});

export const useStore = () => useContext(StoreContext);

const StoreProvider = ({ children }) => {
  const [store, setStore] = useState({ cart: [] });

  const setStoreData = useCallback((data) => {
    setStore(data);
  }, []);

  return (
    <StoreContext.Provider
      value={{
        store,
        setStoreData,
      }}
    >
      {children}
    </StoreContext.Provider>
  );
};

const App = () => {
  const [items, setItems] = useState([]);

  useEffect(() => {
    const fetchItems = async () => {
      const items = await getItems();
      setItems(items);
    };
    fetchItems();
  }, []);

  return (
    <StoreProvider>
      <StripeProvider stripe={stripePromise}>
        <Layout onSearchResult={(items) => setItems(items)}>
          <Items items={items} />
        </Layout>
      </StripeProvider>
    </StoreProvider>
  );
};

export default App;
