import React, { useEffect } from "react";
import ItemCounter from "./item-counter";
import { useStore } from "./App";

const ItemDetail = ({ item = {} }) => {
  const { store, setStoreData } = useStore();
  const [quantity, updateQuantity] = React.useState(1);

  useEffect(() => {
    const cart = store.cart || [];
    const _item = cart.find((i) => i.id === item.id);
    updateQuantity(_item?.quantity || 1);
  }, [item, store.cart]);

  return (
    <div className="item-detail">
      <div className="item-detail-close" data-bs-dismiss="offcanvas">
        <span>X</span>
      </div>
      <div className="item-name">
        <h1>{item.name}</h1>
      </div>
      <div className="item-price">{`€ ${item.price} EUR`}</div>
      <div className="item-image">
        <img src={item.imageUrl} alt={item.name} />
      </div>
      <div className="quantity">
        <ItemCounter quantity={quantity} updateQuantity={updateQuantity} />
      </div>
      <div className="sub-total">
        <span className="sub-total-title">Subtotal :</span>
        <span className="sub-total-price">
          € {parseFloat(item.price * quantity).toFixed(2)}
        </span>
      </div>
      <div className="add-to-cart" data-bs-dismiss="offcanvas">
        <button
          onClick={() => {
            const cart = [...store.cart];
            const cartItemIndex = store.cart.findIndex((i) => i.id === item.id);

            if (cartItemIndex > -1) {
              quantity
                ? cart.splice(cartItemIndex, 1, { ...item, quantity })
                : cart.splice(cartItemIndex, 1);
            } else {
              quantity && cart.push({ ...item, quantity });
            }
            setStoreData({
              ...store,
              cart,
            });
          }}
        >
          <span className="cart-button">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              fill="#887e7e"
              className="bi bi-cart-plus"
              viewBox="0 0 16 16"
            >
              <path d="M9 5.5a.5.5 0 0 0-1 0V7H6.5a.5.5 0 0 0 0 1H8v1.5a.5.5 0 0 0 1 0V8h1.5a.5.5 0 0 0 0-1H9V5.5z" />
              <path d="M.5 1a.5.5 0 0 0 0 1h1.11l.401 1.607 1.498 7.985A.5.5 0 0 0 4 12h1a2 2 0 1 0 0 4 2 2 0 0 0 0-4h7a2 2 0 1 0 0 4 2 2 0 0 0 0-4h1a.5.5 0 0 0 .491-.408l1.5-8A.5.5 0 0 0 14.5 3H2.89l-.405-1.621A.5.5 0 0 0 2 1H.5zm3.915 10L3.102 4h10.796l-1.313 7h-8.17zM6 14a1 1 0 1 1-2 0 1 1 0 0 1 2 0zm7 0a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
            </svg>
          </span>
          <span className="add-to-cart-text">Add to Cart</span>
        </button>
      </div>
    </div>
  );
};

export default ItemDetail;
